//   - Ban.js
// CSS
import "./css/ban.css";
//  
import ban_data from "./data/banner";
// 
import $ from "jquery";

/* 
    [ JS  jQuery    ]
    JS    
      index   
       !
         
          
          
       ! ->  !

    [  :       ! ]
    ) function jqFn(){  }
        function (){
            return(
                <>
                    
                    {jqFn()}
                </>
            )
        }
*/
//  !!! ///////////
function jqFn() {
    $(() => {
        ////////// jQB ////////////////

        // 
        let prot = 0;

        // 1.   
        $(".abtn").click(function () {
            // 0.
            if (prot) return;
            prot = 1;
            setTimeout(() => (prot = 0), 400);

            // 1. 
            let isB = $(this).is(".rb");
            console.log("?", isB);

            //   :    
            const tg = $(this).siblings(".slider");

            // 2.  
            // (1)   :  (left:0->-100%)
            if (isB) {
                tg.animate({ left: "-100%" }, 400, function () {
                    // this !
                    //  li  !  left:0
                    $(this).append($(this).find("li").first()).css({ left: "0" });
                }); /////// animate ///////
            } ///////// if ////////////////

            // (2)   :  (left:-100%->0)
            else {
                //  li + left:-100%  left:0 
                tg.prepend(tg.find("li").last()).css({ left: "-100%" }).animate({ left: "0" }, 400);
            } //////////// else ////////////////

            // 3.     "on"( "on")
            // : .indic li
            // eq() ->  1,  0
            // isB  true->1,  false->0
            //      data-seq !
            $(".indic li")
                .eq(tg.find("li").eq(isB).attr("data-seq"))
                .addClass("on")
                .siblings()
                .removeClass("on");
        }); ////////// click ////////////
    }); ///////////// jQB ////////////////
} ///////////// jqFn  /////////////////

//     ///////
function MakeList(props) {
    // rec - ()
    return (
        <li data-seq={props.idx}>
            <img className="banimg" src={props.rec["src"]} alt="" />
            <section className="bantit">
                <h3>{props.rec["tit1"]}</h3>
                <h2>{props.rec["tit2"]}</h2>
                <p>{props.rec["cont"]}</p>
                <button>{props.rec["btn"]}</button>
            </section>
        </li>
    );
} //////////// MakeList //////////////////

//   /////////
function Ban(props) {
    // props.cat   
    const sel_data = ban_data[props.cat];
    // sel_data  data/banner.js  

    return (
        <div className="banner">
            {/*   */}
            <ul className="slider">
                {sel_data.map((x, i) => (
                    <MakeList rec={x} key={i} idx={i} />
                ))}
            </ul>
            {/*  +   :  2 */}
            {
                //  &&  :  true 
                sel_data.length > 1 && (
                    <>
                        {/*  */}
                        <button className="abtn lb"></button>
                        <button className="abtn rb"></button>
                        {/*   */}
                        <ol className="indic">
                            {sel_data.map((x, i) => (
                                <li className={i == 0 ? "on" : ""} key={i}></li>
                            ))}
                        </ol>
                    </>
                )
            }
            {/* JS/jQuery ! */}
            {jqFn()}
        </div>
    );
} /////////// Ban  /////////////

export default Ban;
