//   
import Logo from "./Logo";
import "./css/layout.css";
import { Link, Outlet } from "react-router-dom";

/******************************************************* 
    [       ]
    1. <Link to="/"></Link>
    -> to  <Route path="/">  !

    2. <Outlet />
    ->     
*******************************************************/

const Layout = () => {



    const menu = [
        {
            txt:"Home",
            link:"/",
        },
        {
            txt:"CHARACTERS",
            link:"/ct",
        },
        {
            txt:"COMICS",
            link:"/co",
            sub:[{txt:"Hi",link:"/hi"},{txt:"My",link:"/my"},{txt:"We",link:"/we"},],
        },
        {
            txt:"MOVIES",
            link:"/mv",
        },
        {
            txt:"GAMES",
            link:"/gm",
        },
        {
            txt:"NEWS",
            link:"/nw",
        },
        {
            txt:"VIDEO",
            link:"/vd",
        },
        {
            txt:"SWIPER",
            link:"/sw",
        },
    ];


    return (
        <>
            {/* 1. */}
            <header className="top">
                {/*   */}
                <nav className="gnb">
                    <ul>
                        <li>
                            <Logo />
                        </li>
                        {
                            menu.map((v,i)=>
                                <li key={i}>
                                    <Link to={v.link}>{v.txt}</Link>
                                    {console.log(v.sub)}
                                    {
                                        v.sub != undefined &&
                                        <ol className="smenu">
                                            {
                                                v.sub.map((v,i)=>
                                                    <li key={i}>
                                                        <Link to={v.link}>{v.txt}</Link>
                                                    </li>
                                                )
                                            }
                                        </ol>
                                        
                                    }
                                </li>                            
                            )
                        }
                    </ul>
                </nav>
            </header>
            {/* 2.  */}
            <main className="cont">
                {/*  :     */}
                <Outlet />
            </main>
            {/* 3. */}
            <footer className="info">
                All Site Content  &amp; TM DC, unless otherwise noted here.
                <br />
                All rights reserved.
            </footer>
        </>
    );
}; ////////// Layout  ///////

// 
export default Layout;
